.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App {
  text-align: center;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.centered-table {
  width: 60%; /* Cambia este valor al porcentaje que prefieras */
  margin-left: auto;
  margin-right: auto;
  border-collapse: collapse;
}

.centered-table th,
.centered-table td {
  border: 1px solid black;
  padding: 8px;
  text-align: center; 
}

.centered-table th {
  background-color: #d5c1db;
}